//////////////////////
// container
//////////////////////

.container {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: @container-fluid;
}

//////////////////////
// grid
//////////////////////

.col {
  flex: 1 1 100%;
  -webkit-flex-basis: auto;
  -ms-flex-preferred-size: 100%; // fix ie10 overflow
  flex-basis: auto;
  max-width: none;
  width: auto;
}

// grid

.col(@number, @breakpoint:none) when (@breakpoint = none) {
  @val: (percentage(@number / @columns));
  -webkit-flex-basis: @val;
  -ms-flex-preferred-size: @val;
  flex-basis: @val;
  max-width: @val;
  width: @val;
}

.col(@number, @breakpoint:none) when (default()) {
  @query-min: 'min-@{breakpoint}';
  @val: (percentage(@number / @columns));
  @media @@query-min {
    -webkit-flex-basis: @val;
    -ms-flex-preferred-size: @val;
    flex-basis: @val;
    max-width: @val;
    width: @val;
  }
}

//////////////////////
// flex
//////////////////////

.flex {
  .display(flex);
}

//////////////////////
// list
//////////////////////

.list-base() {
  .display(flex);
  .reset-list();
  //.flex-auto(); // @FIX flex inside flex take 100% but dont bugs negative margins
  // list
  > li {
    .display(flex);
    .align-items-start();
    > * {
      .flex-auto();
    }
  }
  .list-btn-all({ .block(); .text-align(left); });
  .list-btn-default({ .btn(); });
}

// selectors

.list-btn-all(@rules) {
  > li {
    > a, > button {
      @rules();
    }
  }
}

.list-btn-default(@rules) {
  > li {
    > a, > button {
      &:not(.btn) {
        @rules();
      }
    }
  }
}

// .list

.list {
  .list-base();
  .flex-row();
  .flex-wrap();
}

.list-block {
  .flex-column();
  .flex-nowrap();
}

//////////////////////
// box
//////////////////////

.box {
  .display(flex);
  .flex-column();
  position: relative;
}

